<template>
  <div class="row">
    <div
      is="transition-group"
      class="row"
      appear
      enter-active-class="animated fadeIn"
      leave-actice-class="animated fadeIn"
    >
      <h1>All products</h1>
      Search by name:
      <input v-model="nameFilter" type="text" list="product-names" />
      <datalist id="product-names">
        <option v-for="product in products" v-bind:key="product.id">{{ product.name }}</option>
      </datalist>

      <div class="col-sm-3" v-for="product in filterBy(products, nameFilter, 'name')" v-bind:key="product.id">
        <p>{{ product.name }}</p>
        <p>{{ product.price }}</p>
        <p>{{ product.description }}</p>
        <img v-bind:src="product.main_image_url" height="200px" alt="" />
        <div>
          <a :href="`/products/${product.id}`">More info</a>
        </div>
      </div>
    </div>
  </div>
</template>

<style>
.fade {
  transition: fade-in, 0.2s;
}
</style>

<script>
import axios from "axios";
import Vue2Filters from "vue2-filters";

export default {
  mixins: [Vue2Filters.mixin],
  data: function() {
    return {
      products: [],
      nameFilter: "",
    };
  },
  created: function() {
    this.indexProducts();
  },
  methods: {
    indexProducts: function() {
      axios.get("/api/products").then(response => {
        this.products = response.data;
        console.log("All Products", this.products);
      });
    },
  },
};
</script>
